import 'bootstrap/dist/css/bootstrap.min.css';

import React from "react";
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    NavbarText
} from 'reactstrap';
import {AuthorizedView, useGigyaAuth} from "../gigya";

const LinkButton = props => <button
    {...props}
    type="button"
    className="link-button"
>
    {props.children}
</button>


const Logout = props => {
    const {logout} = useGigyaAuth();
    return <LinkButton {...props} onClick={logout}>Logout</LinkButton>;
}


export function Bar() {
    return (
        <Navbar color="light" light expand="md">
            <NavbarBrand href="/">Id-Booom</NavbarBrand>

            <Nav className="mr-auto" navbar>
                <NavItem>
                    <NavLink href="/home/">Home</NavLink>
                </NavItem>
                <NavItem>
                    <NavLink href="/login/">Login</NavLink>
                </NavItem>
          
                <AuthorizedView>
                    
                    <NavItem>
                        <NavLink href="/profile/">Profile</NavLink>
                    </NavItem>
                    
                    <NavItem>
                        <Logout/> 
                    </NavItem>
                    
                </AuthorizedView>

            </Nav>
        </Navbar>
    );
}
